.registration-form{
    position: absolute;
    top: 0;
    left: -255px;
    max-width: 300px;
    padding: 20px;
    /* border: 1px solid red; */
    border-radius: 15px;
    padding: 20px;
    box-sizing: border-box;
    background: radial-gradient(circle, rgba(103,152,116,1) 29%, rgba(50,98,64,1) 69%);
    z-index: 1;
}

.regist__input {
    width: 260px;
    height: 30px;
    /* border: 1px solid red; */
    padding: 5px;
    border-radius: 12px;
    margin: 15px 0;
    box-shadow: -1px -1px 29px 0px rgba(50, 50, 50, 0.75);
}

.regist__input input {
    width: 100%;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    color:rgba(1,42,13,1);
    outline:none;
    text-align: center;
    padding-top: 5px;
}

.regist__input input::placeholder{
    color: rgba(1,42,13,1);
}

.regist__button  {
    display: block;
    font-size: 20px;
    color: rgba(1,42,13,1);
    margin: 0 auto;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    outline:none;
    padding: 15px 35px;
    border-radius: 15px;
    box-shadow:         0px 0px 30px 0px rgba(103,152,116,1);
}

.regist__button {
    -webkit-animation: regist__button 3s linear infinite;
    animation: regist__button 3s linear infinite;
}
@-webkit-keyframes regist__button {
    0% { box-shadow:         0px 0px 30px 0px rgba(103,152,116,1); }
    50% { box-shadow:         0px 0px 30px 0px rgb(30, 212, 78); }
    100% { box-shadow:         0px 0px 30px 0px rgba(103,152,116,1); }
}
@keyframes regist__button {
    0% { box-shadow:         0px 0px 30px 0px rgba(103,152,116,1); }
    50% {box-shadow:         0px 0px 30px 0px rgb(30, 212, 78); }
    100% { box-shadow:         0px 0px 30px 0px rgba(103,152,116,1); }
}

/* test blink */

.blink1, .blink2 ,.blink3 ,.blink4, .blink5  {
    -webkit-animation: blink1 3s linear infinite;
    animation: blink1 3s linear infinite;
}
@-webkit-keyframes blink1 {
    0% { box-shadow:         0px 0px 30px 0px rgba(103,152,116,1); }
    50% { box-shadow:         0px 0px 30px 0px rgb(30, 212, 78); }
    100% { box-shadow:         0px 0px 30px 0px rgba(103,152,116,1); }
}
@keyframes blink1 {
    0% { box-shadow:         0px 0px 30px 0px rgba(103,152,116,1); }
    50% {box-shadow:         0px 0px 30px 0px rgb(30, 212, 78); }
    100% { box-shadow:         0px 0px 30px 0px rgba(103,152,116,1); }
}

